.todoList {
  position: relative;
  display: flex;
  flex-direction: column;
  /* width: 300px; */
  /* min-width: 150px;
  max-width: 300px; */
  height: 100vh;
  overflow-y: hidden;
  padding: 0;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;

  /* flex: 1 1; */
  /* flex-basis: 200px; */
}

.todoCon {
  overflow-y: scroll;
  height: 100%;
